<template>
  <div id="index">
    <!-- 轮播图 -->
    <Swiper v-bind:options="swiperOption" class="swiper-container">
      <Swiper-slide v-for="(item, index) in slideList" v-bind:key="index">
        <a href="javascript:;">
          <img v-lazy="item.imageUrl" alt="" />
        </a>
        </Swiper-slide>
        <div class="swiper-pagination" slot="pagination"></div>
        <div class="swiper-button-prev" slot="button-prev"></div>
        <div class="swiper-button-next" slot="button-next"></div>
    </Swiper>

      <div class="wrapper">
        <div class="music-container">
               <!--推荐歌单  -->
              <section-header :title="recomm_title" :dataList="recomm_tab" :ismore="true" @update="updateTab" @more="goPlayList"/>
              <div class="section-playlist">
                   <play-list-item
                      class="item"
                      v-for="(item, index) in recomm_playlist"
                      :key="index"
                      :playListId="item.id"
                      :picUrl="item.picUrl"
                      :name="item.name"
                      :playCount="item.playCount"
                      @playlistDetail="goplaylistDetail"
                    />
              </div>
            
              <div class="section">
                <!-- 主栏 -->
                <div class="content">
                  <!-- 新碟首发 -->
                  <section-header :title="disc_title" :dataList="chdisc_tab" :ismore="false" @update="getDiscList"/>
                  <new-disc :discList="discList"></new-disc>
                </div>

                <!-- 侧栏 -->
                <div class="aside">
                   <section-header :title="aside_title" :dataList="chsing_tab" :ismore="true" @update="getsinger"/>
                   <singer-aside :singList="singList"></singer-aside>
                </div>

              </div>

              <!-- 独家放送 -->
              <section-header :title="unique_title" :ismore="false"/>
              <banner :list="unipue_list"/>

              <!-- 最新 MV -->
              <section-header :title="mv_title" :dataList="mv_tab" :ismore="true" @update="getMV" @more="goMv"/>
              <div class="mv-section">
                   <mv-item
                      class="item"
                      v-for="(item, index) in mv_list"
                      :key="index"
                      :mvId="item.id"
                      :cover="item.cover"
                      :name="item.name"
                      :artistName="item.artistName"
                      :artists="item.artists"
                      :playCount="item.playCount"
                    />
              </div>
        </div>  
    </div> 
    
    <nav-footer></nav-footer>
  </div>
</template>
<script>
// 工具包
import { req_introduce_playList, req_update_playList_Tab, req_disc, req_unipue } from "../network/song/index.js";
import { req_singer } from "../network/singer/index.js";
import { req_new_mv } from "../network/mv/index.js";
import { req_swiper } from "../network/swiper/index.js";
import tools from "../tools/formatData.js";

// 轮播图组件
import { Swiper, SwiperSlide } from "vue-awesome-swiper";
import "swiper/css/swiper.css";

// 组件包
import playListItem from '../components/item/playListItem';
import newDisc from "../components/index-components/NewDisc";
import sectionHeader from "../components/index-components/SectionHeader";
import singerAside from "../components/index-components/SingerAside";
import banner from "../components/index-components/Banner";
import mvItem from "../components/item/mvItem";
import navFooter from "../components/navFooter";

export default {
  name: "Index",
  components: {
    Swiper,
    SwiperSlide,
    newDisc,
    sectionHeader,
    singerAside,
    banner,
    mvItem,
    playListItem,
    navFooter,
  },
  data() {
    return {
      // 轮播图配置选项
      swiperOption: {
        autoplay: true,
        loop: true,
        effect: "cube",
        cubeEffect: {
          shadowOffset: 100,
          shadowScale: 0.6,
        },
        pagination: {
          el: ".swiper-pagination",
          clickable: true,
        },
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev",
        },
      },
      // 轮播图数组
      slideList: [],

      // 推荐歌单
      recomm_playlist: [],
      recomm_title: "最新歌单",
      recomm_tab:[ "华语", "流行", "摇滚", "民谣"],

      // 新碟首发
      chdisc_tab: ["华语", "欧美", "韩国", "日本"],
      endisc_tab: [
        { en: "ZH", ch: "华语" },
        { en: "EA", ch: "欧美" },
        { en: "KR", ch: "韩国" },
        { en: "JP", ch: "日本" },
      ],
      discList: [],
      disc_title: "新碟首发",
      disc_page: 1,
      disc_count: 20,

      // 侧栏（歌手）
      aside_title: "歌手列表",
      chsing_tab: ["华语", "欧美", "韩国"],
      ensing_tab: [
        { en: "7", ch: "华语" },
        { en: "96", ch: "欧美" },
        { en: "16", ch: "韩国" },
      ],
      singList: [],
      sing_Num: 25,

      // 独家放送
      unique_title: "独家放送",
      unipue_list: [],

      // MV 视频
      mv_title: "最新 MV",
      mv_list: [],
      mv_tab: ["内地", "港台", "欧美"],
      limit: 8
    };
  },
  watch: {
     mv_list: function() {
        this.mv_list.forEach((e, index, arr) => {
          let value = tools._tranNumber(e.playCount, 2);
          arr[index].playCount = value;
        });
     },
     recomm_playlist: function() {
       this.recomm_playlist.forEach((e, index, arr) => {
         let value = tools._tranNumber(e.playCount, 2);
         arr[index].playCount = value;
       })
     }
  },
  mounted() {
    this.init();
    this.swiper();
    this.getPlayList();
    this.getDiscList();
    this.getsinger();
    this.getunipue();
    this.getMV();
  },
  methods: {
    // 初始化页面环境
    init() {
       document.documentElement.scrollTop = 0;
    },
    // 转义
    getEn(arr, target) {
      return arr.findIndex(e => {
        return e.ch == target;
      });
    },
    // 获取轮播图图片
    swiper() {
      req_swiper(this)
        .then(res => {
          this.slideList = res.banners;
        })
        .catch(error => {
          console.log("获取轮播图出现错误", error);
        });
    },
    // 获取热门歌单
    getPlayList() {
      req_introduce_playList(this, { limit: 10 })
        .then(res => {
          this.recomm_playlist = res.result;
        })
        .catch(error => {
          console.log("获取推荐歌单出现错误", error);
        });
    },
    // 更新歌单类型
    updateTab(cat) {
      req_update_playList_Tab(this, {
         limit: 10,
         cat,
      }).then(res => {
        this.recomm_playlist = res.playlists.map(e => {
          // 解构数据 （取出所需数据）
          let { name, id, playCount, coverImgUrl: picUrl } = e;
          return { name, picUrl, id, playCount };
        });
      });
    },
    // 获取新碟
    getDiscList(area = this.endisc_tab[0].ch) {
      // 转义成相对应英文格式
      let index = this.getEn(this.endisc_tab, area);
      req_disc(this, {
          offset: this.disc_page * this.disc_count,
          limit: this.disc_count,
          area: this.endisc_tab[index].en,
      }).then(res => {
          this.disc_page++;
          this.discList = res.monthData.splice(0, this.disc_count);
        })
        .catch(error => {
          console.log("获取新碟出现错误", error);
        });
    },
    // 获取歌手
    getsinger(area = this.ensing_tab[0].ch) {
      // 转义成相对应英文格式
      let index = this.getEn(this.ensing_tab, area);
      req_singer(this, {
        area: this.ensing_tab[index].en,
      }).then(res => {
          this.singList = res.artists.splice(0, this.sing_Num);
      }).catch(error => {
          console.log("获取歌手出现错误", error);
      });
    },
    // 获取独家放送
    getunipue() {
      req_unipue(this)
        .then(res => {
          this.unipue_list = res.result;
        })
        .catch(error => {
          console.log("获取独家放送出现错误", error);
        });
    },
    // 获取最新 MV
    getMV(area= this.mv_tab[0]) {
      req_new_mv(this, {
         limit: this.limit,
         area,
      }).then(res => {
          this.mv_list = res.data;
      }).catch(error => {
          console.log("获取最新 MV 出现错误", error);
      });
    },
    // 跳转到歌单详情页
    goplaylistDetail(id) {
      this.$router.push({
        path: `/playListDetail/${new Date().getTime()}`,
        query: { id },
      });
    },
    // 歌单更多
    goPlayList() {
       this.$router.push({
          path: `/playList/${new Date().getTime()}`,
       });
    },
    // Mv 更多
    goMv() {
       this.$router.push({
          path: `/mv/${new Date().getTime()}`,
       });
    }
  },
};
</script>
<style lang="scss">
@import "../assets/scss/music.scss";
@import "../assets/scss/swiper.scss";
#index {
  .swiper-container {
    width: $min-width;
    height: 460px;
    margin: 12px auto;
    img {
      width: 1220px;
      height: 100%;
    }
  }
  .wrapper {
    .music-container {
      .section-playlist {
        border-radius: 12px;
        height: 595px;
        @include flex();
        flex-wrap: wrap;
      }
      .section {
        position: relative;
        .content {
          width: 900px;
        }
        .aside {
          position: absolute;
          width: 300px;
          right: 0;
          top: -60px;
          box-sizing: border-box;
        }
      }
      .mv-section {
         height: 421px;
         @include flex();
         flex-wrap: wrap;
      }
    }
  }
}
</style>
