<template>
    <div id="singAside">
        <div class="sing-content">
            <div class="item" v-for="(item, index) in list" :key="index">
               <img v-lazy="item.img1v1Url" alt="">
               <p @click="goSingerDetail(item.id, item.name)">{{item.name}}</p>
            </div> 
        </div>
        <div class="taC">
            <span v-for="(item, index) in list" :key="index" :class="[page==index? 'active': '']" @click="update(index)"></span>
        </div>
    </div>
</template>
<script>
export default {
  name: "singAside",
  props: {
    singList: Array,
  },
  data() {
    return {
      page: 0,
      num: 5,
      list: [],
    };
  },
  watch: {
    singList: function() {
      this.page = 0;
      this.list = this.singList.slice(this.page, this.num);
    },
  },
  methods: {
      update(index) {
          this.page = index;
          this.list = this.singList.slice(this.page * this.num, (this.page + 1) * this.num);
      },
      goSingerDetail(id, name) {
          this.$router.push({
            path: `/singerDetail/${new Date().getTime()}`,
            query: {
              id,
              name,
            }
          })
      }
  },
};
</script>

<style lang="scss">
@import "../../assets/scss/config.scss";
@import "../../assets/scss/music.scss";
#singAside {
  .sing-content {
    width: 300px;
    height: 248px;
    .item {
      position: relative;
      width: 144px;
      height: 144px;
      margin-bottom: 12px;
      float: left;
      margin-right: 12px;
      &:nth-child(2) {
        margin-right: 0px;
      }
      &:nth-child(n + 3) {
        width: 92px;
        height: 92px;
        margin-bottom: 0px;
        p {
          font-size: 12px;
          padding-left: 3px;
        }
      }
      &:last-child {
        margin-right: 0px;
      }
      img {
        display: block;
        height: 100%;
      }
      p {
        position: absolute;
        bottom: 0px;
        height: 20px;
        @include font(14px, 20px, $colorD);
        padding-left: 10px;
        box-sizing: border-box;
        cursor: pointer;
        &:hover {
          color: $colorA;
        }
      }
    }
  }
  .taC {
      width: 73px;
      height: 30px;
      font-size: 0px;
      margin: 20px auto 0px auto;
      @include flex();
      span {
         width: 8px;
         height: 8px;
         border-radius: 50%;
         border: 1px solid #333;
      }
      .active {
          background-color: $colorA;
          border: 1px solid $colorA;
      }
  }
}
</style>
