<template>
  <div id="newDisc">
    <div class="content">
      <div
        class="item"
        v-for="(item, index) in list"
        :key="index"
        @click="detail(item.id)"
      >
        <div class="box">
          <img v-lazy="item.blurPicUrl" alt="">
        </div>
        <div class="name">{{item.name}}</div>
    </div>
  </div>
  <div :class="[catC? 'catC':'']">
    <span
      v-for="(item, index) in list"
      :key="index"
      :class="[page==index? 'active': '']"
      @click="update(index)"
    >{{index+1}}</span>
  </div>
  </div>
</template>
<script>
export default {
  name: "newDisc",
  props: {
    discList: Array,
  },
  components: {},
  data() {
    return {
      page: 0,
      num: 5,
      list: [],
      catC: false,
    };
  },
  watch: {
    discList: function() {
      this.list = this.discList.slice(this.page, this.num);
      this.page = 0;
      // 解决数据渲染与样式渲染不同步问题
      this.catC = true;
    },
  },
  methods: {
    update(index) {
      this.page = index;
      this.list = this.discList.slice(
        this.page * this.num,
        (this.page + 1) * this.num
      );
    },
    detail(id) {
       this.$message({
          showClose: true,
          message: '链接：https://music.163.com/#/album?id=' + id,
          type: 'success'
        });
    }
  },
};
</script>
<style lang="scss">
@import "../../assets/scss/music.scss";
@import "../../assets/scss/config.scss";
#newDisc {
  height: 298px;
  box-sizing: border-box;
  padding-top: 20px;
  .content {
    width: 900px;
    padding: 12px 20px;
    box-sizing: border-box;
    @include flex();
    .item {
      width: 150px;
      height: 152px;
      &:hover {
        .box {
          box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.3);
        }
        .name {
          opacity: 1;
        }
      }
      .box {
        width: 150px;
        height: 130px;
        box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
        background-image: url("/img/icons/coverall.png");
        background-position: 0 -845px;
        background-repeat: no-repeat;
        img {
          position: absolute;
          width: 129px;
          display: block;
          height: 129px;
        }
      }
      .name {
        text-align: center;
        margin-top: 12px;
        @include font(16px, 20px, $colorC);
        @include fontHidden();
        opacity: 0;
        cursor: pointer;
      }
    }
  }
  .catC {
    width: 188px;
    height: 40px;
    margin: 20px auto 0px auto;
    border-radius: 17px;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
    @include flex();
    span {
      display: block;
      width: 40px;
      height: 40px;
      @include font(14px, 40px, rgb(77, 75, 75));
      text-align: center;
      border-radius: 50%;
      cursor: pointer;
    }
    .active {
      background-color: $colorA;
      color: $colorD;
    }
  }
}
</style>
