<template>
  <div id="sectionHeader">
    <div class="title">
      <h3>{{title}}</h3>
      <div class="tab-list">
        <div :class="['tab', curent_index == index? 'active': '']" v-for="(item, index) in dataList" :key="index" @click="update(item, index)">{{item}}</div>
        <div class="tab" v-if="ismore" @click="more">更多</div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "sectionHeader",
  props: {
    title: String,
    dataList: Array,
    ismore: Boolean,
  },
  data() {
    return {
      curent_index: 0,
    };
  },
  methods: {
    update(type, index) {
      this.curent_index = index;
      this.$emit("update", type);
    },
    more() {
      this.$emit("more");
    }
  },
};
</script>
<style lang="scss">
@import "../../assets/scss/music.scss";
@import "../../assets/scss/config.scss";
@import url("https://fonts.googleapis.com/css?family=Raleway:400,400i,700");
#sectionHeader {
  margin-top: 60px;
  .title {
    height: 40px;
    margin-bottom: 20px;
    @include flex();
    border-bottom: 3px solid $colorA;
    h3 {
      cursor: pointer;
      position: relative;
      display: block;
      font: 700 24px Raleway, sans-serif;
      -webkit-text-stroke: 2px $colorA;
      background: linear-gradient(#99eec5 0 100%) left / 0 no-repeat;
      color: transparent;
      background-clip: text;
      transition: 0.5s linear;
      &:hover {
        background-size: 100%;
      }
    }
    .tab-list {
      @include flex();
      .tab {
        width: 40px;
        @include font(14px, 40px, $colorC);
        &:hover {
          color: $colorA;
          cursor: pointer;
        }
      }
      .active {
        color: $colorA;
      }
    }
  }
}
</style>
